@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{margin:0}body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow:hidden}

.app_body{display:flex}

.sidebar{display:flex;flex:0.35 1;height:100vh;flex-direction:column}.sidebar hr{border:1px solid #ededed}.sidebar__header{display:flex;align-items:center;justify-items:space-between;padding:0.8rem;background-color:#ededed}.sidebar__header .sidebar__headerRight{display:flex;align-items:center;margin-left:auto}.sidebar__header .sidebar__headerRight .MuiSvgIcon-root{color:gray}.sidebar__search{display:flex;align-items:center;padding:0.8rem;background-color:#f6f6f6}.sidebar__search .sidebar__searchContainer{flex:1 1;display:flex;align-items:center;background-color:white;padding:0.5rem;border-radius:25px}.sidebar__search .sidebar__searchContainer .MuiSvgIcon-root{margin-right:0.1rem;color:gray}.sidebar__search .sidebar__searchContainer input{flex:1 1;border:none}.sidebar__search .sidebar__searchContainer input:focus{outline:none}.sidebar__search--focus{background-color:white;-webkit-transition:box-shadow .18s ease-out,background-color .25s ease-out;transition:box-shadow .18s ease-out,background-color .25s ease-out;-webkit-transition-property:box-shadow, background-color;transition-property:box-shadow, background-color;-webkit-transition-duration:0.18s, 0.25s;transition-duration:0.18s, 0.25s;-webkit-transition-timing-function:ease-out, ease-out;transition-timing-function:ease-out, ease-out;-webkit-transition-delay:0s, 0s;transition-delay:0s, 0s;box-shadow:0 2px 3px rgba(0,0,0,0.08)}.sidebar__search--focus .sidebar__searchContainer .MuiSvgIcon-root{margin-right:0.1rem;color:#00a5f4}.sidebar__messages{overflow-y:auto;scrollbar-width:thin}

.sidebarMessage{display:flex;align-items:center;padding:0.8rem}.sidebarMessage .sidebarMessage__message{flex:1 1;padding-left:0.3rem}.sidebarMessage .sidebarMessage__message h3{font-size:16px;font-weight:500}.sidebarMessage .sidebarMessage__message p{font-size:14px}.sidebarMessage .sidebarMessage__time{text-align:right}.sidebarMessage .sidebarMessage__time p{font-size:12px}.sidebarMessage .sidebarMessage__time .sidebarMessage__expand{color:white}.sidebarMessage:hover{cursor:pointer;background-color:#f6f6f6}.sidebarMessage:hover .sidebarMessage__time .sidebarMessage__expand{color:gray}

.chat{flex:0.65 1;display:flex;flex-direction:column}

.chatHeader{display:flex;padding:0.8rem;background-color:#ededed;border-bottom:1px solid lightgray}.chatHeader .chatHeader__info{flex:1 1;padding-left:1rem}.chatHeader .chatHeader__info h3{font-weight:500;margin-bottom:5px}.chatHeader .chatHeader__info p{color:gray;font-size:small}.chatHeader .chatHeader__icons{display:flex;justify-content:space-between;min-width:100px}

.chatBody{flex:1 1;height:100vh;background-repeat:repeat;background-position:center;padding:1rem 0.6rem;overflow-y:scroll}

.chatMessage{padding:0 30px}.chatMessage .chatMessage__container{position:relative;background-color:white;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0.8rem 1rem;border-radius:10px;margin-bottom:1rem}.chatMessage .chatMessage__container .chatMessage__senderName{position:absolute;font-size:xx-small;font-weight:800;top:-12px}.chatMessage .chatMessage__container .chatMessage__time{font-size:xx-small;margin-left:10px}.chatMessage .chat__receiver{margin-left:auto;background-color:#dcf8c6}

.welcomeScreen{background-color:#ededed;display:grid;flex:0.65 1;place-items:center}.welcomeScreen .welcomeScreenContainer{text-align:center}.welcomeScreen .welcomeScreenContainer img{width:180px;object-fit:contain}.welcomeScreen .welcomeScreenContainer .welcomeScreen__headerText{font-weight:500;margin-top:1rem}.welcomeScreen .welcomeScreenContainer .welcomeScreen__text{margin-top:1rem;font-weight:100;color:gray}.welcomeScreen .welcomeScreenContainer .welcomeScreen__text span{color:#07bc4c}.welcomeScreen .welcomeScreenContainer hr{margin-top:2rem;border:none;height:1px;background-color:lightgray}.welcomeScreen .welcomeScreenContainer .welcomeScreen__originalSiteLinkContainer{margin-top:1rem}.welcomeScreen .welcomeScreenContainer .welcomeScreen__originalSiteLinkContainer .welcomeScreen__originalSiteLink{display:flex;font-size:14px;align-items:center;justify-content:center;color:gray}.welcomeScreen .welcomeScreenContainer .welcomeScreen__originalSiteLinkContainer .welcomeScreen__originalSiteLink .MuiSvgIcon-root{margin-right:0.2rem}.welcomeScreen .welcomeScreenContainer .welcomeScreen__originalSiteLinkContainer .welcomeScreen__originalSiteLink a{text-decoration:none;color:#07bc4c}

